<template>
	<div class="dealPassword">
		<div class="input-focus">
			<input @input="handleInput" oninput="if(value.length>6)value=value.slice(0,6)" v-model="dealPassword" ref="dealPsd" type="number" v-focus/>
		</div>
		<mu-flex @click="dealPasswordGetFocus" class="flex-wrapper" style="position: relative;" align-items="center">
			<mu-flex class="flex-demo" justify-content="center">
				<p v-if="dealPassword.length>0" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
			</mu-flex>
			<mu-flex class="flex-demo" justify-content="center">
				<p v-if="dealPassword.length>1" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
			</mu-flex>
			<mu-flex class="flex-demo" justify-content="center">
				<p v-if="dealPassword.length>2" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
			</mu-flex>
			<mu-flex class="flex-demo" justify-content="center">
				<p v-if="dealPassword.length>3" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
			</mu-flex>
			<mu-flex class="flex-demo" justify-content="center">
				<p v-if="dealPassword.length>4" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
			</mu-flex>
			<mu-flex class="flex-demo" justify-content="center">
				<p v-if="dealPassword.length>5" style="width: 10px;height: 10px;margin:auto;border-radius: 5px;background: #295DF1;"></p>
			</mu-flex>
		</mu-flex>
	</div>
</template>

<script>
export default {
  name: 'ExportPrivateKey',
  data(){
  	return{

  	}
  },
  props: {
  	value: {
		default: ''
	},
  },
  methods:{
  	dealPasswordGetFocus() {
		this.$refs.dealPsd.focus();
	},
	//密码限制输入数字
	handleInput(e) {
		this.dealPassword = e.target.value.replace(/[^\d]/g, '');
	}
  },
  computed:{
  	dealPassword: {
		get() {
			return this.value
		},
		set(val) {
			return this.$emit('input', val)
		}
	}
  },
  watch:{
  	
  }
 }
</script>

<style scoped>
	.dealPassword{
		
	}
</style>


